import axios from 'axios'

export default {
    namespaced: true,

    state: {
        loading: false,
        ticket: {},
        submitting: false,
    },

    getters: {
        loading: (state) => (state.loading),
        ticket: (state) => (state.ticket),
        submitting: (state) => (state.submitting),
    },

    mutations: {
        setLoading: (state, payload) => (state.loading = payload),
        setTicket: (state, payload) => (state.ticket = payload),
        setSubmitting: (state, payload) => (state.submitting = payload),
    },

    actions: {
        async showRecord({ commit }, data) {
            commit("setLoading", true)

            try {
                const response = await axios.get(`v1/tickets/reason/${data}`, {
                    headers: {
                        Authorization: 'Basic aGVscGxpbmUtc2FuZGJveDpobCEzMX5zYW5kYm94'
                    }
                });

                commit('setTicket', response.data);
            } catch (error) {
                alert("The system encountered an error. Please refresh the page.")
            } finally {
                commit("setLoading", false)
            }
        },

        async storeReason({ commit }, data) {
            commit("setSubmitting", true)

            try {
                const response = await axios.post('v1/tickets/reason', data, {
                    headers: {
                        Authorization: 'Basic aGVscGxpbmUtc2FuZGJveDpobCEzMX5zYW5kYm94'
                    }
                });

                commit('setNotification', {
                    type: response.data.type,
                    message: response.data.message
                }, { root: true })

                commit('setTicket', response.data.ticket);
            } catch (error) {
                alert('The system encountered an error. Please refresh the page.');
            } finally {
                commit("setSubmitting", false)
            }
        },
    }
}
