import axios from 'axios'

export default {
    namespaced: true,

    state: {
    },

    getters: {
    },

    mutations: {
    },

    actions: {
        async doAccept({ commit }, data) {
            try {
                const response = await axios.get(`v1/tickets/accept-resolution/${data}`, {
                    headers: {
                        Authorization: 'Basic aGVscGxpbmUtc2FuZGJveDpobCEzMX5zYW5kYm94'
                    }
                });

                commit('setNotification', {
                    type: response.data.type,
                    message: response.data.message
                }, { root: true })
            } catch (error) {
                alert('The system encountered an error. Please refresh the page.');
            }
        },
    }
}
