const capitalizeFirstLetterEachWord = (str) => {
    if (str) {
        var splitStr = str.toLowerCase().split(' ');
        for (var i = 0; i < splitStr.length; i++) {
            splitStr[i] = splitStr[i].charAt(0).toUpperCase() + splitStr[i].substring(1);
        }

        return splitStr.join(' ');
    }
}

const capitalizeFirstLetterEachWordArray = (arrayData) => {
    let capitalizedArray = [];

    arrayData.forEach(element => {
        if (element.full_name) {
            var splitStr = element.full_name.toLowerCase().split(' ');
            for (var i = 0; i < splitStr.length; i++) {
                splitStr[i] = splitStr[i].charAt(0).toUpperCase() + splitStr[i].substring(1);
            }

            capitalizedArray.push({
                id: element.id,
                full_name: splitStr.join(' ')
            });
        }
    });

    return capitalizedArray
}

export { capitalizeFirstLetterEachWord, capitalizeFirstLetterEachWordArray }